<script>
// @ is an alias to /src
import vue2Dropzone from 'vue2-dropzone'
import 'vue2-dropzone/dist/vue2Dropzone.min.css'
import HeaderInfo from '@/components/HeaderInfo.vue'
import RedLabel from '@/components/RedLabel.vue'

export default {
  name: 'Home',
  components: {
    vue2Dropzone,
    HeaderInfo,
    RedLabel
  },
  data: function () {
    return {
      vmode: 'INITIAL',
      dropzoneOptions: {
        url: '/api/chimei_api',
        method: 'post',
        addRemoveLinks: true,
        dictRemoveFile: '刪除',
        dictRemoveFileConfirmation: '確定刪除？',
        paramName: 'filename',
        acceptedFiles: '.dcm',
        maxFilesize: 100,
        timeout: 300000,
        thumbnailMethod: 'contain',
        headers: {},
        init: function () {
          this.on('success', function (file) {
            this.removeFile(file)
          })

          this.on('error', function () {
            var errorDisplay = document.querySelectorAll(
              '[data-dz-errormessage]'
            )
            errorDisplay[errorDisplay.length - 1].innerHTML = '上傳失敗'
          })
        }
      },
      //回傳分析結果
      analyze: {},
      //影片網址
      dicomMp4Url: null,
      camImg: null,
      endDiastoleImg: null,
      endSystoleImg: null,
      rwma: '',
      ef: 0
    }
  },
  methods: {
    //檔案上傳完成
    onUploaded: function (file, response) {
      this.analyze = response[0]

      this.dicomMp4Url = '/api' + this.analyze.dicom_mp4
      this.camImg = this.getBase64Img(this.analyze.cam)
      this.endDiastoleImg = this.getBase64Img(this.analyze.end_diastole)
      this.endSystoleImg = this.getBase64Img(this.analyze.end_systole)
      this.ef = this.analyze.EF
      this.rwma = this.analyze.RWMA

      this.vmode = 'UPLOADED'
    },
    getBase64Img: function (base64) {
      return 'data:image/jpeg;base64,' + base64
    },
    removeImage: function (id) {
      if (typeof this.predictImages[id] === 'undefined') {
        return
      }

      this.predictImages.splice(id, 1)
    }
  }
}
</script>

